import Vue from 'vue';
import Router from 'vue-router';
import ApplicationPage from './components/ApplicationPage';

Vue.use(Router);

export default new Router(
  {
    routes: [
      {
        path: '/',
        name: 'Application',
        component: ApplicationPage
      }
    ]
  }
);



